import React, { useState } from 'react';
import { createClient } from '@supabase/supabase-js';
import {
  X,
  Save,
  AlertCircle,
  CheckCircle,
  Loader
} from 'lucide-react';
import { UAT_SECTIONS } from '../../config/uat-sections';

const supabase = createClient(
  import.meta.env.VITE_SUPABASE_URL || process.env.NEXT_PUBLIC_SUPABASE_URL,
  import.meta.env.VITE_SUPABASE_ANON_KEY || process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY
);

export function CreateTestCaseModal({ onClose, onSave }) {
  const [formData, setFormData] = useState({
    title: '',
    section: '',
    priority: 'medium',
    test_type: 'manual',
    description: ''
  });
  const [errors, setErrors] = useState({});
  const [saving, setSaving] = useState(false);
  const [success, setSuccess] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    // Clear error for this field
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.title.trim()) {
      newErrors.title = 'Title is required';
    }

    if (!formData.section) {
      newErrors.section = 'Section is required';
    }

    if (!formData.description.trim()) {
      newErrors.description = 'Description is required';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const generateTestId = () => {
    const sectionPrefix = formData.section.toUpperCase().replace(/_/g, '-');
    const timestamp = Date.now().toString(36).toUpperCase();
    return `MANUAL-${sectionPrefix}-${timestamp}`;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    setSaving(true);
    setSuccess(false);

    try {
      const testId = generateTestId();

      // Insert the new test case
      const { error } = await supabase
        .from('uat_cases')
        .insert({
          id: testId,
          section: formData.section,
          title: formData.title,
          priority: formData.priority,
          test_type: formData.test_type,
          description: formData.description
        });

      if (error) throw error;

      setSuccess(true);

      // Call onSave callback
      if (onSave) {
        onSave({
          id: testId,
          ...formData
        });
      }

      // Reset form after a short delay
      setTimeout(() => {
        setFormData({
          title: '',
          section: '',
          priority: 'medium',
          test_type: 'manual',
          description: ''
        });
        setSuccess(false);
      }, 2000);

    } catch (error) {
      console.error('Failed to create test case:', error);
      setErrors({
        submit: error.message || 'Failed to create test case. Please try again.'
      });
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
      <div className="bg-white dark:bg-gray-800 rounded-xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="bg-gradient-to-r from-green-600 to-green-500 dark:from-green-700 dark:to-green-600 text-white p-6 rounded-t-xl">
          <div className="flex items-center justify-between">
            <h2 className="text-2xl font-bold">Create New Test Case</h2>
            <button
              onClick={onClose}
              className="p-2 hover:bg-white/20 rounded-lg transition-colors"
              aria-label="Close modal"
            >
              <X className="h-5 w-5" />
            </button>
          </div>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {/* Success Message */}
          {success && (
            <div className="bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-lg p-4 flex items-center gap-3">
              <CheckCircle className="h-5 w-5 text-green-600 dark:text-green-400" />
              <span className="text-green-800 dark:text-green-300">Test case created successfully!</span>
            </div>
          )}

          {/* Error Message */}
          {errors.submit && (
            <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4 flex items-center gap-3">
              <AlertCircle className="h-5 w-5 text-red-600 dark:text-red-400" />
              <span className="text-red-800 dark:text-red-300">{errors.submit}</span>
            </div>
          )}

          {/* Title */}
          <div>
            <label htmlFor="test-case-title" className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">
              Test Case Title *
            </label>
            <input
              id="test-case-title"
              type="text"
              name="title"
              value={formData.title}
              onChange={handleChange}
              placeholder="Enter a descriptive title for the test case"
              className={`w-full px-4 py-2 border rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-500 dark:placeholder-gray-400 focus:outline-none focus:ring-2 ${
                errors.title
                  ? 'border-red-500 focus:ring-red-500'
                  : 'border-gray-300 dark:border-gray-600 focus:ring-green-500'
              }`}
            />
            {errors.title && (
              <p className="mt-1 text-sm text-red-600 dark:text-red-400">{errors.title}</p>
            )}
          </div>

          {/* Section */}
          <div>
            <label htmlFor="test-case-section" className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">
              Section *
            </label>
            <select
              id="test-case-section"
              name="section"
              value={formData.section}
              onChange={handleChange}
              className={`w-full px-4 py-2 border rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 focus:outline-none focus:ring-2 ${
                errors.section
                  ? 'border-red-500 focus:ring-red-500'
                  : 'border-gray-300 dark:border-gray-600 focus:ring-green-500'
              }`}
            >
              <option value="">Select a section...</option>
              {Object.entries(UAT_SECTIONS).map(([category, options]) => (
                <optgroup key={category} label={category}>
                  {options.map(option => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </optgroup>
              ))}
            </select>
            {errors.section && (
              <p className="mt-1 text-sm text-red-600 dark:text-red-400">{errors.section}</p>
            )}
          </div>

          {/* Priority and Test Type Row */}
          <div className="grid grid-cols-2 gap-4">
            {/* Priority */}
            <div>
              <label htmlFor="test-case-priority" className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">
                Priority
              </label>
              <select
                id="test-case-priority"
                name="priority"
                value={formData.priority}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 focus:outline-none focus:ring-2 focus:ring-green-500"
              >
                <option value="critical">Critical</option>
                <option value="high">High</option>
                <option value="medium">Medium</option>
                <option value="low">Low</option>
              </select>
            </div>

            {/* Test Type */}
            <div>
              <label htmlFor="test-case-type" className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">
                Test Type
              </label>
              <select
                id="test-case-type"
                name="test_type"
                value={formData.test_type}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 focus:outline-none focus:ring-2 focus:ring-green-500"
              >
                <option value="manual">Manual</option>
                <option value="automatic">Automatic</option>
              </select>
            </div>
          </div>

          {/* Description */}
          <div>
            <label htmlFor="test-case-description" className="block text-sm font-semibold text-gray-700 dark:text-gray-300 mb-2">
              Test Description *
            </label>
            <textarea
              id="test-case-description"
              name="description"
              value={formData.description}
              onChange={handleChange}
              placeholder="Describe the steps to execute this test, expected results, and any important notes..."
              rows={6}
              className={`w-full px-4 py-2 border rounded-lg bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-500 dark:placeholder-gray-400 focus:outline-none focus:ring-2 resize-none ${
                errors.description
                  ? 'border-red-500 focus:ring-red-500'
                  : 'border-gray-300 dark:border-gray-600 focus:ring-green-500'
              }`}
            />
            {errors.description && (
              <p className="mt-1 text-sm text-red-600 dark:text-red-400">{errors.description}</p>
            )}
            <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
              Include step-by-step instructions, expected outcomes, and any prerequisites
            </p>
          </div>

          {/* Help Text */}
          <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg p-4">
            <h4 className="font-semibold text-blue-800 dark:text-blue-300 mb-2">
               Tips for Good Test Cases
            </h4>
            <ul className="text-sm text-blue-700 dark:text-blue-400 space-y-1">
              <li> Use clear, action-oriented titles</li>
              <li> Include specific steps in the description</li>
              <li> Define clear pass/fail criteria</li>
              <li> Mark as Critical if failure blocks key functionality</li>
              <li> Choose Manual for user interaction tests</li>
            </ul>
          </div>
        </form>

        {/* Footer */}
        <div className="border-t border-gray-200 dark:border-gray-700 p-6">
          <div className="flex justify-between">
            <button
              type="button"
              onClick={onClose}
              className="px-6 py-2 bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded-lg hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors"
            >
              Cancel
            </button>
            <button
              onClick={handleSubmit}
              disabled={saving || success}
              className={`px-6 py-2 rounded-lg font-semibold transition-colors flex items-center gap-2 ${
                saving || success
                  ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                  : 'bg-green-600 text-white hover:bg-green-700'
              }`}
            >
              {saving && <Loader className="h-4 w-4 animate-spin" />}
              {success ? (
                <>
                  <CheckCircle className="h-4 w-4" />
                  Created!
                </>
              ) : (
                <>
                  <Save className="h-4 w-4" />
                  Create Test Case
                </>
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
